@import '~node_modules/modern-normalize/modern-normalize.css';

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  border: none;
}

body {
  font-family: 'Gilroy', sans-serif;
  color: var(--primary-text-color);
  background-color: var(--bg-color);
  letter-spacing: 0.03em;
  font-weight: 400;
  font-style: normal;
}

.title {
  margin-bottom: 32px;

  font-family: 'Gilroy';
  font-weight: 600;
  font-size: 24px;
  line-height: 120%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: var(--title-text-color);

  @media screen and (min-width: 768px) {
    font-size: 30px;
  }
  @media screen and (min-width: 1440px) {
    font-size: 54px;
    margin-bottom: 50px;
  }
}

.subtitle {
  margin-bottom: 16px;

  font-family: 'Gilroy';
  font-weight: 600;
  font-size: 20px;
  line-height: 125%;

  @media screen and (min-width: 1440px) {
    font-size: 28px;
    margin-bottom: 20px;
  }
}

.text {
  font-family: 'Gilroy';
  font-size: 14px;
  line-height: 125%;

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 145%;
  }

  @media screen and (min-width: 1440px) {
    font-size: 20px;
  }
}
